export default {
  props: ['reply'],
  data() {
    return {
      edit: false,
      replyMode: false,
    }
  },
  methods: {
    update(text) {
      this.$axios
        .patch(`api/reply/${this.reply.id}`, {
          text,
        })
        .then((r) => {
          this.reply = r.data
          this.edit = false
        })
        .catch((e) => {
          this.$message.error(`네트워크 오류. ${e.$message}`)
        })
    },
    closeEdit(v) {
      this.edit = false
    },
    action(act) {
      if (act.edit) {
        this.edit = act.edit
      }
    },
  },
}
